.title-1 {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 5.4rem;
    line-height: 98.5%;
    @include respond(phone) {
        font-size: 32px; } }

.text {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 2.4rem;
    line-height: 116%;
    letter-spacing: 0.04em;
    @include respond(phone) {
        font-size: 20px; } }

